<template>
  <van-popup v-model="couponShow" class="coupon-box">
    <div class="coupon"></div>
    <img class="collectImmediatelyImg" :src="this.$getCdnUrl('life/collectImmediately.png')" @click="getCoupon" alt/>
    <img :src="this.$getCdnUrl('life/couponClose.png')" @click="couponShow=false" alt/>
  </van-popup>
</template>

<script>
  export default {
    name: 'CouponModal',
    data() {
      return {
        couponShow: false,
      };
    },
    methods: {
      /**
       * showCoupon
       * 0 未使用
       * 1 已使用
       * 2 没领过
       * */
      async selectCoupon() {
        const res = await this.http.get(this.api.selectCoupon, {
          openid: this.$wx.getOpenId(),
        }, true);

        this.couponShow = res.data.showCoupon === 2;
      },
      async getCoupon() {
        const res = await this.http.get(this.api.getCoupon, {
          openid: this.$wx.getOpenId(),
        });

        // 领取失败
        if (res.status !== 200) {
          this.$toast.fail(res.message);
          return;
        }

        this.$toast.success(res.message);
        this.couponShow = false;
      },
    },
    created() {
      this.selectCoupon();
    },
  };
</script>

<style lang="scss" scoped>
  .coupon-box {
    text-align: center;
    background-color: rgba(0, 0, 0, 0);

    .coupon {
      width: 375px;
      height: 319px;
      background-image: url("https://images.oraro.net/guoqian/life/coupon.png");
      background-size: 100% 100%;
    }

    .collectImmediatelyImg {
      position: absolute;
      width: 160px;
      height: 48px;
      top: 206px;
      left: 104px;
    }
  }
</style>



